.App {
  text-align: center;
  height:100vh;
  width:100vw;
  overflow: none
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.mapSearch{
    width: 80%;
}
#searchWidget{
    width:80%;
    height:20%;
    pointer-events: auto;
}
#searchWidget .esri-search__input{
    height: 64px;
    font-size: 28px;
}
#searchWidget .esri-search__submit-button{
    height: 64px;
    width: 64px;
}
#searchWidget .esri-icon-search{
    font-size: 32px;
}
#searchWidget .esri-icon-close{
    font-size: 32px;
}
#searchWidget .esri-search__clear-button{
    height: 64px;
    width: 64px;
}
#searchWidget .esri-menu{
    font-size: 23px
}
#searchWidget .esri-icon-locate-circled{
    font-size: 23px;
}
#searchWidget ul{
    text-align: left;
}
.esri-search__form input{
    padding-left: 0.5em;
}

.sidePanel {
    height: 20em;
    overflow-y: auto;
    position: absolute;
    bottom: 0px;
    left: 2em;
    background-color: rgb(245,245,245,0.85);;
    min-width: 280px;
    z-index: 100;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    opacity: 0.85;
    visibility: visible; 
}
.pointerEventsInactive {
    pointer-events : none
}
.pointerEventsActive {
    pointer-events : auto
}
.example-component.hide {
    opacity: 0;
    visibility: hidden; 
}
.sideNavButton {
    height: 30px;
    width: 30px;
    position: absolute;
    top: 20px;
    right: 46px;
    z-index: 100;
}
.infoPanel {
    height: 30em;
    width:30em;
    position: absolute;
    bottom: 0px;
    right: 2em;
    background-color:rgb(245,245,245);
    border: 1px solid cadetblue;
    border-radius: 5px;
    min-width: 280px;
    z-index: 100;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    opacity: 1;
    visibility: visible; 
    overflow: hidden;
}
.header{
    overflow: hidden;
    height:3.2em;
}
.body{
    height: 26.8em;
    padding-top: 0;
    overflow-y:auto;
    overflow-x:hidden;
}
.mapOverlayPanel{
    position: absolute;
    top: 100px;
    right: 0px;
    z-index: 100;
    width: 100%;
    height: 88%;
    pointer-events: none;
}
.map-view {
    width: 100vw;
    height: 100vh;
    margin: 0;
    overflow: hidden;
}
.ReactMap{
    overflow:hidden;
    margin:0;
    padding:0;
}
.esri-widget-button {
    font-size: 1px;
    background-color: #fff;
    color: #6e6e6e;
    width: 40px;
    height: 40px;
    padding: 0;
    margin: 0;
    overflow: hidden;
    cursor: pointer;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transition: background-color 125ms ease-in-out;
    transition: background-color 125ms ease-in-out;
}
.esri-icon-plus, .esri-icon-minus, .esri-icon-pan, .esri-icon-rotate, .esri-icon-compas{
    font-size: 24px;
}
.esri-ui-top-left {
    top: 9px;
    left: 9px;
}
.esri-popup {
    z-index:1000 !important;
}
.esri-popup__content{
    margin: 0
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

